@import '../../styles/breakpoints';

.sb-menu-list-search {
  min-width: 300px;
}

.sb-menu-list-search-input {
  border-color: var(--sbt-theme-tertiary-color-30) !important;
  margin: var(--sbt-spacing-40);
  width: 90% !important;
}

.sb-menu-list-search-collapsible-panel {
  border: none;
  border-bottom: 1px solid var(--sbt-theme-tertiary-color-30);
  max-height: 15vh;
  overflow: auto;

  .sb-collapsible-panel__title {
    align-items: center;
    font-size: var(--sbt-font-size-25);
    height: var(--sbt-spacing-90);
    padding-left: var(--sbt-spacing-30);
  }

  .sb-collapsible-panel__content-inner {
    padding: 0 var(--sbt-spacing-40);
  }

  .sb-collapsible-panel__icon {
    align-self: center;
    margin-left: var(--sbt-spacing-20);
  }
}

.sb-menu-list-search-selected {
  padding: var(--sbt-spacing-10) 0;
}

.sb-menu-list-search-badge {
  --sb-button-color: var(--sbt-theme-color-70);
  background: var(--sbt-white);
  color: var(--sbt-theme-color-70);
  font-size: var(--sbt-font-size-20);
  margin: var(--sbt-spacing-10);
  outline: 1px solid var(--sbt-theme-color-70);
  overflow: hidden;
  white-space: nowrap;

  .sb-button_icon {
    color: var(--sbt-cp-tesseract-70);
  }
}
